import PhotoGalleryStore from './PhotoGalleryStore'
import defaultData from '../data/default-data.json'
import photoFilesListData from '../data/photo-list.json'
import { reaction } from 'mobx'

function loadData() {
  return localStorage.getItem('photoGallery')
}

function saveData(data) {
  return localStorage.setItem('photoGallery', JSON.stringify(data))
}

const savedItems = loadData()
// debugger

const initialData = savedItems ? JSON.parse(savedItems) : defaultData

const store = PhotoGalleryStore.create({
  ...initialData,
  photoList: {
    files: photoFilesListData,
    basePath: 'photos',
  },
})

reaction(() => store.output, saveData)

window.store = store
export default store
