import React from 'react'
import { Observer } from 'mobx-react-lite'
import useMst from '../../hooks/useMst'
import RowLayout from '../../components/row-layout/RowLayout'
import NavigationButton from '../../components/navigation-button/NavigationButton'
import './gallery-view.css'

export default function GalleryView() {
  const store = useMst()
  const firstRow = store?.galleryRowList?.[0]
  if (!firstRow) return null

  return (
    <Observer>
      {() => (
        <div className="gallery-view">
          {store.galleryRowList.map((row) => (
            <RowLayout key={row.id} row={row} editMode={false} />
          ))}
          <NavigationButton link="/">Presentación</NavigationButton>
        </div>
      )}
    </Observer>
  )
}
